.card-container{
   display: flex;
   flex-direction: column;
   background-color: rgb(223, 94, 143);
   border:1px solid rgb(34, 31, 31);
   border-radius: 5px;
   padding: 25px;
   cursor: pointer;
   transition: transform 0.25s ease-out;
}

.card-container:hover{
    transform: scale(1.05);
}